@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;400;500;600;700&display=swap);
.center-area {
	/* color: #dfe4e1; */
	color: #282d30;
	padding: 2em;
	font-size: 1rem;
}

.left-area {
	/* color: #dfe4e1; */
	color: #282d30;
	padding: 2em;
	font-size: 1.2rem;
}

.right-area {
	/* color: #dfe4e1; */
	color: #282d30;
	padding: 2em;
	font-size: 0.94rem;
	word-spacing: 0.5rem;
	line-height: 1.4rem;
}

.home-intro-text {
	font-size: "5rem";
	position: absolute;
	width: 80%;
	height: 20%;
	text-align: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

@media (min-width: 992px) {
	.navbar li {
		margin-left: 1em;
		margin-right: 1em;
	}
}

